import React from 'react';
import { RenderMarkProps } from '@ali/4ever-cangjie';

import { MatchMark } from '../mo/marks';
import MarkView from '../components/markView';

const renderMark = (props: RenderMarkProps, _, next) => {
  const { mark } = props;
  if (MatchMark.isMatchMark(mark)) {
    return <MarkView {...props} />;
  }
  return next();
};

export default renderMark;
